<template>
    <div class="mw-page page nodeDetail" v-nav="{hideNavbar:true}">
        <div class="mw-page-header">
            <i class="left icon ion-ios-arrow-back" @click="$router.go(-1)"></i>
            <span v-if="action === 'new'" >添加物料</span>
            <span v-else>物料详情</span>
            <i class="right"></i>
        </div>
        <div class="mw-page-content ">
          <cst-item v-show="matTypeName === '0'"
                    label="类别" v-model="selectInfo.matTypeName " :action="action" :canEdit="false" :hasArrow="true" @click.native="selectList('TypeName')" >
          </cst-item>
          <cst-item v-show="matTypeName!=='2'"
                    label="名称" v-model="mate_info.matName " :action="action" :canEdit="false" :hasArrow="true" @click.native="selectList('matName')" >
          </cst-item>
          <cst-item v-show="matTypeName === '1'"
                    label="规格型号" v-model="mate_info.spec " :action="action" :canEdit="false" :hasArrow="true" @click.native="selectList('spec')" >
          </cst-item>
          <item >
            <span>形状：</span>
            <label><input name="shape" type="radio" value="1" :disabled="action==='check'" v-model="mate_info.shape"/> 立方体</label>
            <label><input name="shape"  type="radio" value="2" :disabled="action==='check'" v-model="mate_info.shape"/> 圆柱体</label>
          </item>
          <cst-item class="noneBorder" v-show="mate_info.shape==='1'" :placeholder="'0'" label="长(mm)" v-model="mate_info.length"  :action="action" editType="number"></cst-item>
          <cst-item class="noneBorder" v-show="mate_info.shape==='1'" :placeholder="'0'" label="宽(mm)" v-model="mate_info.width" :action="action" editType="number"></cst-item>
          <cst-item class="noneBorder" v-show="mate_info.shape==='1'" :placeholder="'0'" label="高(mm)" v-model="mate_info.height" :action="action" editType="number"></cst-item>
          <cst-item class="noneBorder" v-show="mate_info.shape==='2'" :placeholder="'0'" label="高(mm)" v-model="mate_info.height" :action="action" editType="number"></cst-item>
          <cst-item class="noneBorder" v-show="mate_info.shape==='2'" :placeholder="'0'" label="直径(mm)" v-model="mate_info.length" :action="action" editType="number"></cst-item>
          <!--<item>-->
            <!--<span>材质：</span>-->
            <!--<span v-if="action==='check'||matTypeName === '1' ">{{ mate_info.rawnatureName }}</span>-->
            <!--<span v-else class="item-note" @click="selectList('rawnatureName')">-->
              <!--{{ mate_info.rawnatureName }}-->
              <!--<i class="icon ion-ios-arrow-right" style="color: #DDD;"></i>-->
            <!--</span>-->
            <!--</item>-->
          <cst-item v-if="action==='check'||matTypeName === '1' " label="材质" v-model="mate_info.rawnatureName" :action="action" :canEdit="false" editType="number"></cst-item>
          <cst-item v-else label="材质" v-model="mate_info.rawnatureName " :action="action" :canEdit="false" :hasArrow="true" @click.native="selectList('rawnatureName')" ></cst-item>

          <cst-item v-show="mate_info.shape==='2'" label="物料单价" :placeholder="'0'" v-model="mate_info.planPrice" :action="action" editType="number"></cst-item>
          <cst-item v-show="matTypeName!=='1'" label="重量" :placeholder="'0'" v-model="mate_info.weight" :action="action" editType="number"></cst-item>{{newPrice}}
          <cst-item v-show="matTypeName !== '1'" label="计划单价"  :placeholder="'0'" v-model="mate_info.planPrice" :action="action" editType="number"></cst-item>
          <item  v-show=" matTypeName === '0' ">
            <span>生成下层物料</span>
            <label><input name="isPurchase"  :disabled="action==='check'" type="radio" value="1" v-model="node_info.isPurchase"/> 是</label>
            <label><input name="isPurchase"  :disabled="action==='check'" type="radio" value="0" v-model="node_info.isPurchase"/> 否</label>
          </item>
          <item  v-show=" matTypeName === '1' ">
            <span>入库是否质检</span>
            <label><input disabled name="isQc" type="radio" value="1" v-model="mate_info.isQc"/> 是</label>
            <label><input disabled name="isQc" type="radio" value="0" v-model="mate_info.isQc"/> 否</label>
          </item>

          <item v-if="matTypeName==='2'&&action==='new'" style="margin-top: 1px">
            数量 :
            <p >
              粗(个):<input :disabled="action==='check'" :placeholder="'0'" type="number" v-model="node_info.processesRCount">
              中(个):<input :disabled="action==='check'" :placeholder="'0'" type="number" v-model="node_info.processesZCount">
              精(个):<input :disabled="action==='check'" :placeholder="'0'" type="number"v-model="node_info.processesFCount">
            </p>
          </item>
          <cst-item v-else label="数量" v-model="node_info.totalQty"  :action="action" editType="number"></cst-item>
          <item  v-show=" matTypeName === '2' ">
            <span>供应类型</span>
            <label><input :disabled="action!=='new'" name="procureStyle" type="radio" value="1" v-model="mate_info.procureStyle"/> 采购</label>
            <label><input :disabled="action!=='new'" name="procureStyle" type="radio" value="2" v-model="mate_info.procureStyle"/> 下料</label>
            <label><input :disabled="action!=='new'" name="procureStyle" type="radio" value="3" v-model="mate_info.procureStyle"/> 库存</label>
          </item>
          <item  style="height: 120px">
            备注 :
            <textarea :placeholder="action==='check'?'':'请输入备注'" :disabled="action==='check'" class="remark" type="text" v-model="node_info.remark"></textarea>
          </item>
        </div>
        <div class="select-box" :class="selectInfo.selectSwitch?'active':''">
          <div class="select-click" @click="selectInfo.selectSwitch=false"></div>
          <div class="select-main">
            <div class="main-header">
              <p>{{ selectInfo.title }}</p>
              <input type="text">
            </div>
            <div class="main-list">
              <p v-for=" (item,index) in selectInfo.list "  @click="checked(item,index)">{{ item }}</p>
            </div>
          </div>
        </div>
        <div v-show="this.action!=='check'&&$route.params.app_flag!=='step30'" class="mw-page-footer">
            <div class="btns">
                <div class="btn" @click="saveBtn">
                    保存
                </div>
                <div v-show="edit==='edit'" class="btn" @click="cancelEdit">
                  取消
                </div>
            </div>
        </div>
        <div v-show="this.action==='check'&&($route.params.app_flag!=='step30'&&$route.params.app_flag!=='step0')" class="mw-page-footer">
          <div class="btns">
            <div class="btn" @click="clickEdit">
              编辑
            </div>
          </div>
        </div>
    </div>
</template>
<script>
  import CstItem from "../../../components/cstItem";
  //{新建或保存Bom节点，查询物料类别，查询物料名称，物料名称级联查询，物料类型级联名称查询，材质列表，计划价格计算,获取BOM节点详情}
  import { updateNodes,cascadeType,mateName,matNameSpec,cascadeMat,rawnatureList,rawnaturePrice,bomNodeInfo } from '@/api/mats'
  export default{
    name:'bomNodeDetail',
    components: {CstItem},
    data(){
      return {
        action:'',
        node_id:'',
        mat_no:'',
        edit:'',
        matTypeName:'',
        selectInfo:{
          title:'',
          selectSwitch:false,
          selectType:'',
          rawnatureIndex:-1,//被选中材质的下标
          matTypeName:"",//根据该类型做级联
          CheckedList:{//已选选项CheckedList.TypeName
            TypeName:false,//物料类别
            matName:false,     //物料名称
            rawnatureName:'',//材料名称
            spec:false
          },
          list:[]
        },
        cascadeData:{//级联数据
          //物料类别类型列表cascadeData.mat_name_list
          cascade_type_list:[],
          //物料名称列表matName
          mat_name_list:[],
          //材质列表
          rawnature_list:[],
          //物料名称级联规格型号列表
          spec_list:[]
        },
        mate_info:{
          height:'',
          length:'',
          isQc:'',
          matId:'',
          matName:'请选择',
          matNameId:'',
          matNo:'',
          matTypeId:'1',
          matTypeName:'',
          picPath:'',
          planPrice:'' ,
          procureStyle:'',
          rawnatureId:'',
          rawnatureName:'请选择',
          shape:'0',
          spec:'请选择',
          weight:'',
          width:'',
        },
        node_info:{
          isPurchase:1,
          matId:'',
          matType:'',
          nodeId:'',
          processesFCount:'',
          processesRCount:'',
          processesZCount:'',
          remark:'',
          totalQty:1,
        }
      }
    },
    computed:{
      newPrice:function(){
        if(this.matTypeName==='2'){
          if(this.mate_info.height!==0&&this.mate_info.length!==0&&this.mate_info.rawnatureId!==''){
            if(this.mate_info.shape==='1'&&this.mate_info.width!==0){
              this.rawnaturePrice()
            }else if(this.mate_info.shape==='2'){
              this.rawnaturePrice()
            }
          }
        }
        let index=this.selectInfo.rawnatureIndex
        if(index!==-1&&this.mate_info.height!==0&&this.mate_info.length!==0){
          if(this.mate_info.shape==='1'&&this.mate_info.width!==0){
            this.rawnaturePrice()
          }else if(this.mate_info.shape==='2'){
            this.rawnaturePrice()
          }
        }
      }
    },
    methods: {
      cancelEdit(){
        this.action='check'
        this.edit=''
      },
      clickEdit(){
        this.action='action'
        this.edit='edit'
        this.selectInfo.matTypeName=this.mate_info.matTypeName
        if(this.matTypeName==='0'){
          this.cascadeType()
          this.rawnatureList()
        }else if(this.matTypeName==='1'){
          this.mateName()
        }else if(this.matTypeName==='2'){
          this.rawnatureList()
        }
      },
      //updateNodes,cascadeType,mateName,matNameSpec,cascadeMat,rawnatureList,rawnaturePrice
      cascadeType(){//物料类别查询
        const requestData={ }
        cascadeType(requestData).then(res=>{
          this.cascadeData.cascade_type_list=res.cascade_type_list
        }).catch(error=>{
          $toast.show(error.description)
        })

      },
      mateName(){//物料名称列表查询
        const requestData={ }
        mateName(requestData).then(res=>{
          this.cascadeData.mat_name_list=res.mat_name_list
        }).catch(error=>{
          $toast.show(error.description)
        })

      },
      matNameSpec(){//物料名称级联规格查询
        const requestData={
          query:{
            mat_name:this.mate_info.matName
          }
        }
        matNameSpec(requestData).then(res=>{
          this.cascadeData.spec_list=res.mat_info_list
          this.selectInfo.list=[]
          res.mat_info_list.forEach((e,i)=>{
            this.selectInfo.list.push(e.spec)
          })
        }).catch(error=>{
          $toast.show(error.description)
        })
      },
      cascadeMat(){//物料类型级联名称查询
        const requestData={
          query:{
            mat_type:this.selectInfo.matTypeName
          }
        }
        cascadeMat(requestData).then(res=>{
          this.cascadeData.mat_name_list =  res.listCascadeMat

        }).catch(error=>{
          $toast.show(error.description)
        })
      },
      rawnatureList(){//获取材质列表
        const requestData={
          query:{
            isUnused:'',
            rawnatureName:'',
            rawnatureType:''
          }
        }
        rawnatureList(requestData).then(res=>{
          this.cascadeData.rawnature_list=res.rawnature_list
        }).catch(error=>{
          $toast.show(error.description)
        })
      },
      bomNodeInfo(){//获取材质列表
        const requestData={
          node_id:this.node_id
        }
        bomNodeInfo(requestData).then(res=>{
          this.mate_info=res.bom_node_info.mat
          this.node_info.isPurchase=res.bom_node_info.isPurchase
          this.node_info.totalQty=res.bom_node_info.totalQty
          this.node_info.remark=res.bom_node_info.remark
          this.node_info.nodeId=res.bom_node_info.nodeId
          this.node_info.matType=res.bom_node_info.matType
          if(this.node_info.matType==='标准件'){
          }else{
            this.node_info.matId=''
            this.selectInfo.matTypeName=this.mate_info.matTypeName
          }
          if(this.mate_info.isQc!=='1'){
            this.mate_info.isQc='0'
          }
        }).catch(error=>{
          $toast.show(error.description)
        })
      },
      rawnaturePrice(){//计算价格
        if(this.mate_info.shape==='1'){
          this.mate_info.spec=this.mate_info.length+'x'+this.mate_info.width+'x'+this.mate_info.height
        }else if(this.mate_info.shape==='2'){
          this.mate_info.spec='Φ:'+this.mate_info.length+'  H:'+this.mate_info.height
        }
        const requestData={
          height:this.mate_info.height,
          length:this.mate_info.length,
          width:this.mate_info.width,
          rawnature_id:this.mate_info.rawnatureId,
          shape:this.mate_info.shape,
        }
        rawnaturePrice(requestData).then(res=>{
          this.mate_info.planPrice=res.planPrice
          this.mate_info.weight=res.weight
        }).catch(error=>{
          $toast.show(error.description)
          this.mate_info.planPrice=0
          this.mate_info.weight=0
        })
      },
      saveBtn(){//保存
        if(this.matTypeName==='0'){
          let weight=this.mate_info.weight
          if(this.node_info.totalQty>0){
            if(weight>0){
              this.update()
            }else{
              $toast.show('物料信息不全或填写错误，请重新填写')
            }
          }else{
            $toast.show('物料数量有误，请重新填写')
          }
        }else if(this.matTypeName==='1'){
          if(this.mate_info.rawnatureId!==''){
            this.update()
          }else{
            $toast.show('物料信息不全或填写错误，请重新填写')
          }
        }else if(this.matTypeName==='2'){
          if(this.mate_info.rawnatureId!==''&&this.mate_info.procureStyle!==''){
            if(this.node_info.processesFCount!=='0'||this.node_info.processesRCount!=='0'||this.node_info.processesZCount!=='0'){
              this.update()
            }else{
              $toast.show('请填写新建电极数量')
            }

          }else{
            $toast.show('物料信息不全或填写错误，请重新填写')
          }
        }
      },
      update(){//创建或修改BOM节点数据提交
        var pNodeId=''
        if(this.edit==='edit'){
          pNodeId=''
        }else{
          pNodeId=this.node_id
        }
        const requestData={
          mat_info:this.mate_info,
          node_info:this.node_info,
          pNodeId:pNodeId
        }
        updateNodes(requestData).then(res=>{
          if(res===true){
            if(this.edit!=='edit'){
              $toast.show('添加新物料成功')
              $router.go(-1)
            }else{
              $toast.show('物料修改成功')
              this.action='check'
              this.edit=''
            }
          }
        }).catch(error=>{
          $toast.show(error.description)
        })
      },
      selectList(type){
        if(this.action==='check'){
          return
        }
        if(this.matTypeName==='0'){
          //TypeName:物料类别   matName:物料名称 rawnatureName:材质
          switch (type){
            case 'TypeName':{
              this.selectInfo.title='请选择物料类别';
              this.selectInfo.list=this.cascadeData.cascade_type_list
              break
            }
            case 'matName':{
              if (this.selectInfo.CheckedList.TypeName){
                this.selectInfo.title='请选择物料'
                this.selectInfo.list=[];
                this.cascadeData.mat_name_list.forEach((e,i)=> {
                  this.selectInfo.list.push(e.matCascadeName)
                })
              }else{
                $toast.show('请先选择物料类别')
                return
              }
              break
            }
            case 'rawnatureName':{
              if (this.selectInfo.CheckedList.matName){
                this.selectInfo.title='请选择物料材质'
              }else{
                $toast.show('请先选择物料名称')
                return
              }
              break
            }
          }
        }else if(this.matTypeName==='1'){
          switch (type){
            case 'matName':{
              this.selectInfo.list=this.cascadeData.mat_name_list
              this.selectInfo.title='请选择物料名称';
              break
            }
            case 'spec':{
              if (this.selectInfo.CheckedList.matName){
                this.selectInfo.title='请选择物料规格型号'
              }else{
                $toast.show('请先选择物料名称')
                return
              }
              break
            }
          }
        }else if(this.matTypeName==='2'){
          switch (type){
              case 'rawnatureName':{
                this.selectInfo.title='请选择物料材质'
                this.cascadeData.rawnature_list.forEach((e,i)=>{
                  this.selectInfo.list.push(e.raw_nature)
                })
                break
              }
          }
        }
        this.selectInfo.selectType=type
        this.selectInfo.selectSwitch=true
      },
      checked(item,index){
        if(this.matTypeName==='0'){
          switch (this.selectInfo.selectType){
            case 'TypeName':{
              this.selectInfo.matTypeName=item//选定物料类别
              this.cascadeMat();//物料类型级联名称查询，并清除侧滑列表数据，加载物料名称数据
              this.selectInfo.CheckedList.TypeName=true//标记物料类别已选，可选物料名称；并清空 已选物料名称，清空已选材质
              this.mate_info.matName='请选择'//清除已选物料名称
              this.mate_info.rawnatureName ='请选择'//清除已选物料材质
              this.selectInfo.CheckedList.matName=false//标记物料名称未选，禁止选物料材质
              break
            }
            case 'matName':{
              this.mate_info.matName = item//选定物料名称
              this.node_info.matNo=this.cascadeData.mat_name_list[index].matCascadeNo
              this.node_info.matId=this.cascadeData.mat_name_list[index].id//选定物料ID
              if(this.edit==='edit'){
                this.mate_info.matId=this.node_info.matId
              }
              this.selectInfo.CheckedList.matName=true//标记物料名称已选
              this.mate_info.rawnatureName ='请选择'//清除已选物料材质
              this.mate_info.rawnatureId =''
              this.selectInfo.list=[];//清除侧滑列表数据
              this.cascadeData.rawnature_list.forEach((e,i)=>{//侧滑选项框加载材质列表
                this.selectInfo.list.push(e.raw_nature)
              })
              break
            }
            case 'rawnatureName':{
              this.selectInfo.rawnatureIndex=index//选中材质，记住选中材质的下标值，便于读取材质信息
              this.mate_info.rawnatureId=this.cascadeData.rawnature_list[index].raw_nature_id//选定材质ID
              this.mate_info.rawnatureName = item//选定材质
              break
            }
          }
        }else if(this.matTypeName==='1'){
          switch (this.selectInfo.selectType){
            case 'matName':{
              this.mate_info.matName = item//选定物料名称
              this.selectInfo.CheckedList.matName=true
              this.matNameSpec()
              this.mate_info.spec='请选择'
              this.mate_info.rawnatureName=''
              this.mate_info.planPrice=''
              break
            }
            case 'spec':{
              this.mate_info.spec=item
              this.mate_info.rawnatureId=this.cascadeData.spec_list[index].rawnature_id
              this.mate_info.matId=this.cascadeData.spec_list[index].mat_id//选定物料ID
              this.node_info.matId=this.mate_info.matId
              this.mate_info.rawnatureName=this.cascadeData.spec_list[index].rawnature_name
              this.mate_info.planPrice=this.cascadeData.spec_list[index].plan_price
              if(this.cascadeData.spec_list[index].is_qc!=='1'){
                this.mate_info.isQc=0
              }else{
                this.mate_info.isQc=1
              }
            }
          }
        }else if(this.matTypeName==='2'){
          switch (this.selectInfo.selectType){
            case 'rawnatureName':{
              this.mate_info.rawnatureId=this.cascadeData.rawnature_list[index].raw_nature_id//选定材质ID
              this.mate_info.rawnatureName = item//选定材质
              this.mate_info.matName='电极材料'
              break
            }
          }
        }

        this.selectInfo.selectSwitch=false
      }
    },
    mounted(){
      this.node_id = this.$route.params.node_id
      this.action = this.$route.params.action
      this.matTypeName=this.$route.params.matTypeName
      if(this.matTypeName==='0'){
        if(this.action==='new'){
          this.mate_info.matTypeName = '半成品'
          this.node_info.matType='半成品'
          this.selectInfo.matTypeName = '请选择类别'
          this.cascadeType();
          this.rawnatureList();
        }else if(this.action==='check'){
          this.bomNodeInfo()
        }
      }else if(this.matTypeName==='1'){
        if(this.action==='new'){
          this.mate_info.matTypeName = '标准件'
          this.node_info.matType='标准件'
          this.selectInfo.CheckedList.TypeName=true
          this.mateName()
        }else if(this.action==='check'){

          this.bomNodeInfo()
        }
      }else if(this.matTypeName==='2'){
        this.node_info.matType='电极'
        this.mate_info.matTypeName='电极'
        if(this.action=='new'){
          this.rawnatureList()
        }else{
          this.bomNodeInfo()
        }

      }

    }
  }
</script>

<style lang="scss" scoped>

  textarea{width: 100%;resize: none;height: 50px;background: #fff}
</style>



